.animation {
  animation: 1s page;
  background-color: #48B54E;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
}

@keyframes page {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(100%);
  }
}

@keyframes movie {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0px);
  }
}

body {
  font-family: 'Mukta', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 300;
  color: #fff;
  margin: 0;
  height: 100%;
  width: 100%;
  position: relative;
}

/* Reset */
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.title {
  margin: 0;
  font-size: 16px;
  line-height: 1.2;
  color: #000;
  font-weight: 300;
  margin-bottom: 8px;
  margin-top: 6px;
}

.title--big {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 16px;
}

.title--uppercase {
  text-transform: uppercase;
}

.title--meduim {
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 700;
}

.title--center {
  text-align: center;
}

@media (min-width: 1100px) {
  .title--center {
    margin-bottom: 24px;
  }
}

.title--light {
  color: #fff;
}

.title--section {
  border-bottom: 1px solid #000;
  width: calc(100% - 48px);
  margin-top: 0;
  padding-bottom: 6px;
  margin-bottom: 32px;
}

@media (min-width: 1100px) {
  .title--section {
    margin-left: 16px;
  }
}

.title--liste {
  margin-left: 24px;
  border-bottom: 1px solid #000;
  width: calc(100% - 48px);
  margin-top: 24px;
  padding-bottom: 6px;
}

@media (min-width: 500px) {
  .title--liste {
    width: calc(100% - 96px);
    margin-left: 48px;
    margin-top: 32px;
  }
}

.note {
  display: inline-block;
  font-size: 16px;
  padding: 1px 10px;
  border: 2.5px solid #48B54E;
  border-radius: 4px;
  margin-right: 16px;
  color: #48B54E;
}

@media (min-width: 500px) {
  .note {
    margin-right: 32px;
  }
}

.titre {
  color: #48B54E;
  text-transform: uppercase;
  font-size: 32px;
  display: flex;
  flex-direction: column;
}

.titre:after {
  margin-top: 16px;
  content: '';
  width: 100px;
  height: 6px;
  background-color: #48B54E;
}

.titre--section {
  margin: 32px;
}

@media (min-width: 500px) {
  .titre--section {
    margin-top: 132px;
    text-align: center;
  }
  .titre--section:after {
    content: '';
    margin-top: 16px;
    width: 100px;
    height: 6px;
    background-color: #48B54E;
    margin-left: 50%;
    transform: translateX(-50%);
  }
}

.titre--small {
  font-size: 16px;
  position: relative;
}

@media (min-width: 500px) {
  .titre--small {
    margin: 0;
    margin-bottom: 16px;
  }
}

.titre--small:after {
  margin-top: 8px;
  content: '';
  width: 70px;
  height: 3px;
  background-color: #48B54E;
}

